@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #1d1d1d;
  font-family: 'Montserrat', sans-serif;
  color: #fff;
  scroll-behavior: smooth;
}

.App-header {
  max-width: 80%;
  margin: 0 auto;
  text-align: center;
}

h1 {
  font-size: 4rem;
  margin: 0;
}

p {
  font-size: 2rem;
  margin: 2rem 0;
}

.media-container {
  display: flex;
  justify-content: space-around;
  margin: 3rem 0;
}

.media {
  width: 45%;
  height: 0;
  padding-bottom: 56.25%;
  position: relative;
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.description-container {
  display: flex;
  justify-content: space-around;
  max-width: 80%;
  margin: 0 auto;
}

.description {
  width: 45%;
  margin: 2rem 0;
}

h2 {
  font-size: 3rem;
  margin: 0;
}

a {
  display: inline-block;
  font-size: 2rem;
  margin: 1rem 0;
  padding: 1rem;
  border-radius: 5px;
  background-color: #fff;
  color: #1d1d1d;
}